.Bhagwa{
color:white;
font-size: 10vw;
font-weight: 3rem;
}
.grid{
    display: grid;
    grid-template-columns:repeat(auto-fit,minmax(50px,1fr));
    text-align: center;
}
.glow {
    width: 100px;
    height: 100px;
    background-color: white;
    border-radius: 50%;
    box-shadow: 0 0 10px white; /* Initial box-shadow */
    animation: glowAnimation 1s infinite alternate; /* Animation for glowing effect */
  }

  @keyframes glowAnimation {
    0% {
      box-shadow: 0 0 10px white;
    }
    100% {
      box-shadow: 0 0 20px white, 0 0 30px white, 0 0 40px white; /* Adjust the glow intensity as needed */
    }
  }

  /* .glow-container {
    display: inline-block;
  } */

  .glow-text {
    /* font-size: 24px; */
    color: white;
    text-shadow: 0 0 10px white;
    animation: glowAnimation 1s infinite alternate;
  }

  @keyframes glowAnimation {
    0% {
      text-shadow: 0 0 10px white;
    }
    100% {
      text-shadow: 0 0 15px white, 0 0 15px white, 0 0 15px white;
    }
  }