body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  background: url('https://i.pinimg.com/originals/5d/ab/b9/5dabb9a1e56f7aeafa82b19c4c1e1ebc.jpg') no-repeat  fixed;
  background-size: cover;
}
@media screen and (max-width:580px) {
  body{
    background: url('https://i.pinimg.com/originals/5d/ab/b9/5dabb9a1e56f7aeafa82b19c4c1e1ebc.jpg') no-repeat center fixed;
    background-size: cover;

  }
}